
import { defineComponent } from "vue";

export default defineComponent({
  name: "BarraLateral",
  emits: ['aoAlterarModo'],
  data () {
    return {
      modoEscuro: false
    }
  },
  methods: {
    alterarModo () : void {
      this.modoEscuro = !this.modoEscuro
      this.$emit('aoAlterarModo', this.modoEscuro)
    }
  },
  computed: {
    textoBtn () : string {
      return this.modoEscuro ? 'claro' : 'escuro'
    }
  }
});
